.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f4f4f4;
}

.header-banner {
  width: 100%;
}

.header-capa {
  width: 100%;
  height: auto;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  width: 90%;
  max-width: 1200px;
}

.logo img {
  height: 50px;
  margin-right: 10px;
}

h1 {
  font-size: 24px;
  color: #333;
}